.DropZone {
  background-color: #f9fbfc;
}

.DropZone .DottedBox {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column nowrap;
      flex-flow: column nowrap;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  padding: 30px;
  max-width: 100%;
  max-height: 100%;
  width: 768px;
  height: 480px;
  background-color: white;
  border: 2px dashed #cbcbcd;
  color: #cbcbcd;
  -webkit-transition: border-color 0.25s ease-out;
  -o-transition: border-color 0.25s ease-out;
  transition: border-color 0.25s ease-out;
  cursor: pointer;
}

.DropZone .DottedBox p {
  text-align: center;
}

.DropZone .DottedBox p span {
  color: #009000;
}

.DropZone .DottedBox .LnrUpload {
  font-size: 36px;
  -webkit-transition: all 0.25s linear;
  -o-transition: all 0.25s linear;
  transition: all 0.25s linear;
}

.DropZone.Error .DottedBox {
  border-color: #cc0000;
}

.DropZone.Error p {
  color: #cc0000;
}

.DropZone.Success {
  -webkit-animation: success 2s 1;
          animation: success 2s 1;
}

.DropZone.Success .DottedBox {
  border-color: #009000;
}

.DropZone.Success p, .DropZone.Success .LnrUpload {
  color: #009000;
}

.DropZone.Active .DottedBox {
  border-color: #009000;
}

.DropZone.Active .DottedBox .LnrUpload {
  -webkit-animation: pulse 1s infinite;
          animation: pulse 1s infinite;
}

.SampleDownload {
  position: fixed;
  bottom: 5px;
  right: 30px;
  color: #009000;
}

.SampleDownload p {
  margin-top: 0;
}

.SampleDownload::before {
  content: '->';
  position: absolute;
  width: 15px;
  right: calc(100% + 15px);
  -webkit-animation: pulseX 1s infinite;
          animation: pulseX 1s infinite;
}

@-webkit-keyframes pulse {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }

  50% {
    -webkit-transform: translateY(-5px);
            transform: translateY(-5px);
    color: #009000;
  }

  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes pulse {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }

  50% {
    -webkit-transform: translateY(-5px);
            transform: translateY(-5px);
    color: #009000;
  }

  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@-webkit-keyframes pulseX {
  0% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }

  50% {
    -webkit-transform: translateX(-5px);
            transform: translateX(-5px);
  }

  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes pulseX {
  0% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }

  50% {
    -webkit-transform: translateX(-5px);
            transform: translateX(-5px);
  }

  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@-webkit-keyframes success {
  0% {
    opacity: 1;
  }

  25% {
    opacity: 1;
  }

  75% {
    opacity: 0;
  }

  100% {
    opacity: 0;
  }
}

@keyframes success {
  0% {
    opacity: 1;
  }

  25% {
    opacity: 1;
  }

  75% {
    opacity: 0;
  }

  100% {
    opacity: 0;
  }
}



