.SvgElement {
  border: 1px solid #d9d9d9;
  border-radius: 4px;
}

.SvgElement .g-axis-y path[class^="domain"], .SvgElement .g-axis-y path[class*=" domain"] {
  display: none;
}

.SvgElement .g-axis-y line {
  stroke: #d9d9d9;
}

.SvgElement .g-axis-x line {
  stroke: #d9d9d9;
}



